<script setup>
/* eslint-disable */ 

import { onMounted } from 'vue'

onMounted(() => {
    const canvas = document.getElementById("title");
    const ctx = canvas.getContext("2d");
    canvas.height = window.innerHeight;
    canvas.width = window.innerWidth;
    let particleArray = [];
    const adjustX = 0;
    const adjustY = 0;

    // handle mouse 

    const mouse = {
        x: null,
        y: null,
        radius: 50
    }

    window.addEventListener("mousemove", (event) => {
        mouse.x = event.x;
        mouse.y = event.y - 88;
    })
    ctx.fillStyle = "white";
    ctx.font = "30px Roboto";
    ctx.fillText("404 not found", 0, 40);

    const textCoordinates = ctx.getImageData(0, 0, 800, 100);

    class Particle {
        constructor(x, y) {
            this.x = x;
            this.y = y;
            this.size = Math.random() * 1.2 + 1;
            this.baseX = this.x;
            this.baseY = this.y;
            this.density = (Math.random() * 30) + 1;
        }
        draw(glow){
            ctx.fillStyle = "#FBB741";
            ctx.shadowColor = "red";
            ctx.shadowBlur = glow;
            ctx.beginPath();
            ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }
        update(){
            let dx = mouse.x - this.x;
            let dy = mouse.y - this.y;
            let distance = Math.sqrt(dx ** 2 + dy ** 2);
            let forceDirectionX = dx / distance;
            let forceDirectionY = dy / distance;
            let maxDistance = mouse.radius
            let force = (maxDistance - distance) / maxDistance;
            let directionX = forceDirectionX * force * this.density;
            let directionY = forceDirectionY * force * this.density;
            if (distance < mouse.radius){
                this.x -= directionX;
                this.y -= directionY;
            } else {
                if (this.x !== this.baseX) {
                    let dx = this.x - this.baseX;
                    this.x -= dx/10;
                }
                if (this.y !== this.baseY) {
                    let dy = this.y - this.baseY;
                    this.y -= dy/10;
                }
            }
        }
    }

    const init = () => {
        particleArray = [];
        for (let y = 0, y2 = textCoordinates.height; y < y2; y++) {
            for (let x = 0, x2 = textCoordinates.width; x < x2; x++) {
                if (textCoordinates.data[(y * 4 * textCoordinates.width) + (x * 4) + 3] > 244) {
                    let positionX = x + adjustX;
                    let positionY = y + adjustY;
                    particleArray.push(new Particle(positionX * 5, positionY * 5));
                }
            }
        }
    }

    init();

    const animate = () => {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        canvas.height = window.innerHeight;
        canvas.width = window.innerWidth;
        for (let i = 0; i < particleArray.length; i++){
            particleArray[i].draw(10 * Math.random());
            particleArray[i].update();
        }
        requestAnimationFrame(animate);
    }
    animate();

})
</script>

<template>
    <canvas id="title" class="w-full"></canvas>
</template>
